import { Connection, PublicKey, Signer, SYSVAR_CLOCK_PUBKEY, TransactionInstruction } from '@solana/web3.js'
import bs58 from 'bs58'
import { ACCESS_METHOD, encodeText, STAKE_ACCOUNT_ROLE } from '../utils'
import { createCreateProfileInstruction, createRpcPermitInstruction, FarmConfig, Profile } from '../dyfarm'
import { rpcRequestAuth, rpcVerifyAuth } from '../services'

export const getIsRpcPermitted = async (
  publicKey: PublicKey,
  connection: Connection,
  role: string,
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  try {
    const [userProfile] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
      args.programId
    )
    const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
    const label = (Object.values(STAKE_ACCOUNT_ROLE).find((row) => row.val === Number(profile.role)) ??
      STAKE_ACCOUNT_ROLE.builder)['label']
    if (label !== role) return null

    return !profile.offchainPermit ? false : true
  } catch (e) {
    return false
  }
}

export const getIsProfileCreated = async (
  publicKey: PublicKey,
  connection: Connection,
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  try {
    const [userProfile] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
      args.programId
    )
    const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
    if (profile.nftStaked) {
      return true
    } else {
      return false
    }
  } catch (e) {
    return false
  }
}

export const createRpcPermitInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  role: string,
  args: { programId: PublicKey }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const [farmConfigAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const farmConfig = await FarmConfig.fromAccountAddress(connection, farmConfigAccount, 'processed')

  const profile = await connection.getParsedAccountInfo(userProfile, 'processed')
  if (!profile || !profile.value) {
    instructions.push(
      createCreateProfileInstruction(
        {
          user: publicKey,
          userProfile,
          farmConfig: farmConfigAccount,
          clock: SYSVAR_CLOCK_PUBKEY
        },
        {
          role: STAKE_ACCOUNT_ROLE[role].val,
          access: ACCESS_METHOD.nft.val
        },
        args.programId
      )
    )
  }

  instructions.push(
    createRpcPermitInstruction(
      {
        user: publicKey,
        authority: farmConfig.rpc,
        profile: userProfile
      },
      {
        status: true,
        role: STAKE_ACCOUNT_ROLE[role].val
      },
      args.programId
    )
  )

  return { instructions, signers }
}

export const getRpcAuthToken = async (
  publicKey: PublicKey,
  signMessage: (message: Uint8Array) => Promise<Uint8Array>,
  args: {
    rpcHost: string
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!signMessage) throw new Error('signMessage is undefined')

  const preToken = await rpcRequestAuth(publicKey.toBase58(), { rpcHost: args.rpcHost })
  if (!preToken) throw new Error('Request RPC Auth is failed')

  const encodedPreToken = encodeText(preToken)
  const signedMessage = await signMessage(encodedPreToken)
  const signature = bs58.encode(signedMessage)

  const token = await rpcVerifyAuth(publicKey.toBase58(), signature, { rpcHost: args.rpcHost })
  if (!token) throw new Error('Verify RPC Auth is failed')

  return token
}
