import { Connection, PublicKey, Signer, SYSVAR_CLOCK_PUBKEY, TransactionInstruction } from '@solana/web3.js'
import * as splToken from '@solana/spl-token'
import BN from 'bn.js'
import { ACCESS_METHOD, getAssociateTokenAccount, METADATA_PROGRAM_ADDRESS, STAKE_ACCOUNT_ROLE } from '../utils'
import {
  CampaignActivity,
  createClaimRewardInstruction,
  createCreateProfileInstruction,
  createStakeCampaignInstruction,
  createStakeNftInstruction,
  createUnstakeCampaignInstruction,
  Profile
} from '../dyfarm'
import { PCampaign } from '../typings'
import {
  getBuilderActivityInfo,
  getBuilderRecentSubmissions,
  getValidatorActivityInfo,
  getValidatorRecentValidations
} from '../services'

export const createStakeCampaignInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  role: number,
  amount: number,
  campaignTitle: string,
  args: {
    programId: PublicKey
    snsMint: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)

  const profile = await connection.getParsedAccountInfo(userProfile, 'processed')
  if (!profile || !profile.value) {
    instructions.push(
      createCreateProfileInstruction(
        {
          user: publicKey,
          userProfile,
          farmConfig,
          clock: SYSVAR_CLOCK_PUBKEY
        },
        {
          role: role,
          access: ACCESS_METHOD.nft.val
        },
        args.programId
      )
    )
  }

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignTitle)],
    args.programId
  )
  const userToken = getAssociateTokenAccount(args.snsMint, publicKey)
  const [campaignActivity] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignTitle), publicKey.toBuffer()],
    args.programId
  )
  const [campaignVault] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:VAULT'), Buffer.from(campaignTitle)],
    args.programId
  )

  const accountInfo = await connection.getAccountInfo(userToken, 'processed')
  if (accountInfo == null) {
    instructions.push(
      splToken.createAssociatedTokenAccountInstruction(
        publicKey,
        userToken,
        publicKey,
        args.snsMint,
        splToken.TOKEN_PROGRAM_ID,
        splToken.ASSOCIATED_TOKEN_PROGRAM_ID
      )
    )
  }

  instructions.push(
    createStakeCampaignInstruction(
      {
        user: publicKey,
        userProfile,
        campaignActivity,
        userToken,
        campaignAccount,
        campaignVault,
        farmConfig,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      {
        campaignTitle: campaignTitle,
        amount: new BN(amount)
      },
      args.programId
    )
  )

  return { instructions, signers }
}

export const createUnstakeCampaignInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  args: {
    programId: PublicKey
    snsMint: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignTitle)],
    args.programId
  )
  const [campaignVault] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:VAULT'), Buffer.from(campaignTitle)],
    args.programId
  )
  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [dyfVault] = PublicKey.findProgramAddressSync([pdaAccount.toBuffer(), Buffer.from('DYF:VAULT')], args.programId)
  const [campaignActivity] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignTitle), publicKey.toBuffer()],
    args.programId
  )
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const userToken = getAssociateTokenAccount(args.snsMint, publicKey)

  instructions.push(
    createUnstakeCampaignInstruction(
      {
        user: publicKey,
        userProfile,
        userToken,
        campaignAccount,
        campaignVault,
        dyfVault,
        campaignActivity,
        farmConfig,
        pdaAccount,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      {
        campaignTitle: campaignTitle
      },
      args.programId
    )
  )

  return { instructions, signers }
}

