export const createBatchClaimRewardInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitles: string[],
  args: {
    programId: PublicKey
    snsMint: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const userToken = getAssociateTokenAccount(args.snsMint, publicKey)
  const [farmConfig] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)
  const [dyfVault] = PublicKey.findProgramAddressSync([pdaAccount.toBuffer(), Buffer.from('DYF:VAULT')], args.programId)

  const instructions: TransactionInstruction[] = campaignTitles.map((campaignTitle) => {
    const [campaignActivity] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignTitle), publicKey.toBuffer()],
      args.programId
    )
    const [campaignVault] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:CAMPAIGN:VAULT'), Buffer.from(campaignTitle)],
      args.programId
    )

    return createClaimRewardInstruction(
      {
        user: publicKey,
        userProfile,
        userToken,
        campaignActivity,
        campaignVault,
        farmConfig,
        pdaAccount,
        dyfVault,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      {
        campaignTitle: campaignTitle
      },
      args.programId
    )
  })
  const signers: Signer[] = []

  return { instructions, signers }
}

export const createStakeCampaignWithNFTInstructions = async (
  publicKey: PublicKey,
  connection: Connection,
  role: string,
  mint: string,
  args: {
    programId: PublicKey
    snsMint: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const instructions: TransactionInstruction[] = []
  const signers: Signer[] = []

  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const [farmConfigAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF:CONFIG')], args.programId)
  const userNft = getAssociateTokenAccount(new PublicKey(mint), publicKey)
  const [nftVault] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:VAULT'), userNft.toBuffer()],
    args.programId
  )
  const [nftMetadata] = PublicKey.findProgramAddressSync(
    [Buffer.from('metadata'), new PublicKey(METADATA_PROGRAM_ADDRESS).toBuffer(), new PublicKey(mint).toBuffer()],
    new PublicKey(METADATA_PROGRAM_ADDRESS)
  )
  const [pdaAccount] = PublicKey.findProgramAddressSync([Buffer.from('DYF')], args.programId)

  const profile = await connection.getParsedAccountInfo(userProfile, 'processed')
  if (!profile || !profile.value) {
    instructions.push(
      createCreateProfileInstruction(
        {
          user: publicKey,
          userProfile,
          farmConfig: farmConfigAccount,
          clock: SYSVAR_CLOCK_PUBKEY
        },
        {
          role: STAKE_ACCOUNT_ROLE[role].val,
          access: ACCESS_METHOD.nft.val
        },
        args.programId
      )
    )
  }

  instructions.push(
    createStakeNftInstruction(
      {
        user: publicKey,
        userProfile,
        userNft,
        nftVault,
        farmConfig: farmConfigAccount,
        pdaAccount,
        nftMint: new PublicKey(mint),
        nftMetadata,
        clock: SYSVAR_CLOCK_PUBKEY
      },
      args.programId
    )
  )

  return { instructions, signers }
}

export const getTotalAvailableRewards = async (
  publicKey: PublicKey,
  connection: Connection,
  appCampaigns: PCampaign[],
  args: {
    programId: PublicKey
  }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const [userProfile] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
    args.programId
  )
  const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
  const role = Number(profile.role)

  if (role !== STAKE_ACCOUNT_ROLE.builder.val && role !== STAKE_ACCOUNT_ROLE.validator.val) {
    throw new Error('Architects cannot claim!')
  }

  const stakedCampaigns = appCampaigns.filter(
    (campaign) =>
      (role === STAKE_ACCOUNT_ROLE.builder.val && campaign.builderStakeStatus) ||
      (role === STAKE_ACCOUNT_ROLE.validator.val && campaign.validatorStakeStatus)
  )

  const campaignTitles: string[] = []
  let totalRewards = 0
  let totalClaimed = 0
  await Promise.all(
    stakedCampaigns.map(async (campaign) => {
      if (!publicKey) return

      try {
        const [campaignActivityAccount] = PublicKey.findProgramAddressSync(
          [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaign.campaignTitle), publicKey.toBuffer()],
          args.programId
        )
        const campaignActivity = await CampaignActivity.fromAccountAddress(
          connection,
          campaignActivityAccount,
          'processed'
        )

        const unclaimedReward = new BN(campaignActivity.unclaimedReward).toNumber()
        const claimedReward = new BN(campaignActivity.claimedReward).toNumber()

        totalRewards += unclaimedReward
        totalClaimed += claimedReward
        if (unclaimedReward > claimedReward) campaignTitles.push(campaign.campaignTitle)
      } catch (err) {
        /* empty */
      }
    })
  )

  return {
    rewards: totalRewards,
    claimed: totalClaimed,
    campaignTitles
  }
}

export const getBuilderActivity = async (publicKey: PublicKey, args: { apiHost: string; apiAuth: string }) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  const activityInfo = await getBuilderActivityInfo(publicKey.toBase58(), {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  return activityInfo
}

