import { Connection, LAMPORTS_PER_SOL, PublicKey } from '@solana/web3.js'
import BN from 'bn.js'
import { Campaign, CampaignActivity, Profile, Role } from '../dyfarm'
import { checkWhitelist, getAllCampaignsInfo, getCampaginMeta, getCampaignInfo, getRpcListActivity } from '../services'
import { ApiCampaignInfo, PCampaign } from '../typings'
import { decodeText, STAKE_ACCOUNT_ROLE } from '../utils'

export const getCampaignFromCampaignAccount = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  args: { apiHost: string; apiAuth: string; programId: PublicKey; stopOffset: number; deployment: string }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignMeta = await getCampaginMeta(campaignTitle, {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  if (!campaignMeta) return

  const [campaignAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN'), Buffer.from(campaignMeta.dapp_title)],
    args.programId
  )

  // @ts-ignore
  let campaign: Campaign = undefined
  try {
    campaign = await Campaign.fromAccountAddress(connection, campaignAccount, 'processed')
  } catch (e) {
    /* empty */
  }
  if (campaign === undefined) return

  const [campaignActivityAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignMeta.dapp_title), publicKey.toBuffer()],
    args.programId
  )

  let stakeInfo = {
    builderStakeStatus: false,
    validatorStakeStatus: false
  }
  try {
    const campaignActivity = await CampaignActivity.fromAccountAddress(connection, campaignActivityAccount, 'processed')
    const [userProfile] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
      args.programId
    )
    const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
    if (!new BN(campaignActivity.stakeAmount).eqn(0)) {
      const label = Object.values(STAKE_ACCOUNT_ROLE).find((row) => row.val === Number(profile.role))?.label
      stakeInfo = {
        builderStakeStatus: label === STAKE_ACCOUNT_ROLE.builder.label,
        validatorStakeStatus: label === STAKE_ACCOUNT_ROLE.validator.label
      }
    }
  } catch (e) {
    /* empty */
  }

  return {
    campaignTitle: campaignMeta.dapp_title,
    tag: campaign.tag,
    pubkey: campaignAccount.toBase58(),
    industry: decodeText(campaign.industry),
    domain: decodeText(campaign.domain),
    subject: decodeText(campaign.subject),
    explain: campaignMeta.explain,
    organizer: decodeText(campaign.organizer),
    language: decodeText(campaign.lang),
    specific: campaignMeta.phrase_specific,
    general: campaignMeta.phrase_general,
    cause: campaignMeta.phrase_cause,
    effect: campaignMeta.phrase_effect,
    open: new BN(campaign.open).toNumber(),
    stop: new BN(campaign.close).toNumber() - args.stopOffset * 24 * 3600,
    close: new BN(campaign.close).toNumber(),
    expire: new BN(campaign.expire).toNumber(),
    minStake: new BN(campaign.minStake).toNumber(),
    minPhrase: Number(campaign.minPhrase),
    minValidate: Number(campaign.minValidate),
    rpuSpecific: new BN(campaign.rpuSpecific).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuGeneral: new BN(campaign.rpuGeneral).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuCause: new BN(campaign.rpuCause).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuEffect: new BN(campaign.rpuEffect).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    rpuValidator: new BN(campaign.rpuValidator).divn(LAMPORTS_PER_SOL / 100).toNumber() / 100,
    majorityQuorum: Number(campaign.majorityQuorum),
    architect: campaign.architect.toBase58(),
    finish: campaign.finish,
    utterances: [],
    progress: 0,
    submissions: 0,
    rejections: 0,
    timestamp: Math.round(Date.now() / 1000),
    deployment: args.deployment,
    ...stakeInfo
  } as PCampaign
}

export const getCampaignFromCampaignInfo = async (
  publicKey: PublicKey,
  connection: Connection,
  campaignTitle: string,
  role: number,
  args: { apiHost: string; apiAuth: string; programId: PublicKey; stopOffset: number }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignInfo = await getCampaignInfo(campaignTitle, {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  if (!campaignInfo) return

  const [campaignActivityAccount] = PublicKey.findProgramAddressSync(
    [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignInfo.campaignTitle), publicKey.toBuffer()],
    args.programId
  )

  let stakeInfo = {
    builderStakeStatus: false,
    validatorStakeStatus: false
  }
  try {
    const campaignActivity = await CampaignActivity.fromAccountAddress(connection, campaignActivityAccount, 'processed')
    if (!new BN(campaignActivity.stakeAmount).eqn(0)) {
      const label = Object.values(STAKE_ACCOUNT_ROLE).find((row) => row.val === role)?.label
      stakeInfo = {
        builderStakeStatus: label === STAKE_ACCOUNT_ROLE.builder.label,
        validatorStakeStatus: label === STAKE_ACCOUNT_ROLE.validator.label
      }
    }
  } catch (e) {
    /* empty */
  }

  return {
    ...campaignInfo,
    stop: campaignInfo.close - args.stopOffset * 24 * 3600,
    minStake: 100, // @TODO
    utterances: [],
    ...stakeInfo
  } as PCampaign
}

export const getAllCampaigns = async (
  publicKey: PublicKey,
  connection: Connection,
  isFetchingStakedInfoOnchain: boolean,
  args: { apiHost: string; apiAuth: string; programId: PublicKey; rpcHost: string; stopOffset: number }
) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  if (!connection) throw new Error('Connection is undefined')

  const campaignsInfo: ApiCampaignInfo[] = await getAllCampaignsInfo({
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })

  let role: string | null = null
  try {
    const [userProfile] = PublicKey.findProgramAddressSync(
      [Buffer.from('DYF:PROFILE'), publicKey.toBuffer()],
      args.programId
    )
    const profile = await Profile.fromAccountAddress(connection, userProfile, 'processed')
    role = Role[profile.role].toLowerCase()
  } catch (e) {
    /* empty */
  }

  let campaigns: PCampaign[] = []
  if (!isFetchingStakedInfoOnchain) {
    const stakedCampaignAccounts: string[] =
      role !== null
        ? await getRpcListActivity(args.programId.toBase58(), publicKey.toBase58(), {
            rpcHost: args.rpcHost
          })
        : []

    campaigns = campaignsInfo.map((campaignInfo) => {
      return {
        ...campaignInfo,
        stop: campaignInfo.close - args.stopOffset * 24 * 3600,
        minStake: 100, // @TODO
        utterances: [],
        builderStakeStatus:
          role === null
            ? false
            : role !== STAKE_ACCOUNT_ROLE.builder.label
            ? false
            : stakedCampaignAccounts.includes(campaignInfo.pubkey),
        validatorStakeStatus:
          role === null
            ? false
            : role !== STAKE_ACCOUNT_ROLE.validator.label
            ? false
            : stakedCampaignAccounts.includes(campaignInfo.pubkey)
      }
    })
  } else {
    const maxCounter = 20
    const batchLength = Math.ceil(campaignsInfo.length / maxCounter)

    const batchCampaignsInfo: ApiCampaignInfo[][] = []
    for (let i = 0; i < batchLength; i++) {
      const arr: ApiCampaignInfo[] = []
      for (let j = 0; j < maxCounter; j++) {
        if (i * maxCounter + j < campaignsInfo.length) {
          arr.push(campaignsInfo[i * maxCounter + j])
        }
      }
      batchCampaignsInfo.push(arr)
    }

    let i = 0
    let isOnePromiseEnded = true
    while (i < batchCampaignsInfo.length) {
      if (isOnePromiseEnded) {
        isOnePromiseEnded = false

        await Promise.all(
          batchCampaignsInfo[i].map(async (campaignInfo) => {
            const [campaignActivityAccount] = PublicKey.findProgramAddressSync(
              [Buffer.from('DYF:CAMPAIGN:ACTIVITY'), Buffer.from(campaignInfo.campaignTitle), publicKey.toBuffer()],
              args.programId
            )

            let stakeInfo = {
              builderStakeStatus: false,
              validatorStakeStatus: false
            }
            try {
              const campaignActivity = await CampaignActivity.fromAccountAddress(
                connection,
                campaignActivityAccount,
                'processed'
              )
              if (!new BN(campaignActivity.stakeAmount).eqn(0)) {
                const label = Object.values(STAKE_ACCOUNT_ROLE).find((row) => row.label === role)?.label
                stakeInfo = {
                  builderStakeStatus: label === STAKE_ACCOUNT_ROLE.builder.label,
                  validatorStakeStatus: label === STAKE_ACCOUNT_ROLE.validator.label
                }
              }
            } catch (e) {
              /* empty */
            }

            campaigns.push({
              ...campaignInfo,
              stop: campaignInfo.close - args.stopOffset * 24 * 3600,
              minStake: 100, // @TODO
              utterances: [],
              ...stakeInfo
            })
          })
        )

        isOnePromiseEnded = true
        i++
      }
    }
  }

  return campaigns
}

export const getAppRole = async (publicKey: PublicKey, args: { apiHost: string; apiAuth: string }) => {
  if (!publicKey) throw new Error('PublicKey is undefined')
  const role = await checkWhitelist(publicKey.toBase58(), {
    apiHost: args.apiHost,
    apiAuth: args.apiAuth
  })
  return role === null
    ? null
    : ((STAKE_ACCOUNT_ROLE[role] ? STAKE_ACCOUNT_ROLE[role].val : STAKE_ACCOUNT_ROLE.builder.val) as number)
}
